<template>
	<view class="container">
		<view class="content-box">
			<view class="text title1 f-center">《隐私政策》</view>
			<view class="text title2">引言</view>
			<view class="text">网税宝严格遵守法律法规，遵循以下隐私保护原则，为您提供更加安全、可靠的服务：</view>
			<view class="text title3">1、安全可靠：</view>
			<view class="text">我们竭尽全力通过合理有效的信息安全技术及管理流程，防止您的信息泄露、损毁、丢失。</view>
			<view class="text title3">2、自主选择：</view>
			<view class="text">我们为您提供便利的信息管理选项，以便您做出合适的选择，管理您的个人信息。</view>
			<view class="text title3">3、保护通信秘密：</view>
			<view class="text">我们严格遵照法律法规，保护您的通信秘密，为您提供安全的通信服务。</view>
			<view class="text title3">4、合理必要：</view>
			<view class="text">为了向您和其他用户提供更好的服务，我们仅收集必要的信息。</view>
			<view class="text title3">5、清晰透明：</view>
			<view class="text">我们努力使用简明易懂的表述，向您介绍隐私政策，以便您清晰地了解我们的信息处理方式。</view>
			<view class="text title3">6、将隐私保护融入产品设计：</view>
			<view class="text">我们在产品或服务开发的各个环节，综合法律、产品、设计等多方因素，融入隐私保护的理念。</view>
			<view class="text">本《隐私政策》主要向您说明：我们收集哪些信息、我们收集信息的用途、您所享有的权利。</view>
			<view class="text">希望您仔细阅读《隐私政策》（以下简称“本政策”），详细了解我们对信息的收集、使用方式，以便您更好地了解我们的服务并作出适当的选择。</view>
			<view class="text text-indent">在使用网税宝服务前，请您务必仔细阅读并透彻理解本政策所述内容，在确认充分理解并同意后再开始使用。如果您不同意本政策的内容，将可能导致我们的产品和服务无法正常运行，您应立即停止访问/使用我们的产品与/或服务。您使用或继续使用网税宝服务，都表示您同意我们按照本政策（包括更新版本）收集、使用、储存和分享您的个人信息。</view>
			<view class="text">除另有约定外，本政策所用术语与《网税宝服务协议》中的术语具有相同的涵义。</view>
			<view class="text">如您有问题，请联系我们。</view>
			<view class="text title3">我们收集的信息</view>
			<view class="text">我们根据合法、正当、必要的原则，仅收集实现产品功能所必要的信息。</view>
			<view class="text title3">您在使用我们服务时主动提供的信息</view>
			<view class="text">·您在注册帐户时填写的信息</view>
			<view class="text">例如，您在注册网税宝帐户时所填写的昵称、手机号码。</view>
			<view class="text">·您在使用服务时上传的信息</view>
			<view class="text">例如，您在使用设备租赁、物业租赁时，上传的营业执照、身份证、办公环境照片。</view>
			<view class="text">·您通过我们的客服或参加我们举办的活动时所提交的信息</view>
			<view class="text">例如，您参与我们线上活动时填写的调查问卷中可能包含您的姓名、电话、家庭地址等信息。</view>
			<view class="text">我们的部分服务可能需要您提供特定的个人敏感信息来实现特定功能。若您选择不提供该类信息，则可能无法正常使用服务中的特定功能，但不影响您使用服务中的其他功能。若您主动提供您的个人敏感信息，即表示您同意我们按本政策所述目的和方式来处理您的个人敏感信息。</view>
			<view class="text">我们在您使用服务时获取的信息</view>
			<view class="text">·日志信息。当您使用我们的服务时，我们可能会自动收集相关信息并存储为服务日志信息。</view>
			<view class="text title3">o1)设备信息</view>
			<view class="text">例如，设备型号、操作系统版本、唯一设备标识符、OAID、电池、信号强度等信息。</view>
			<view class="text title3">o2)软件信息</view>
			<view class="text">例如，软件的版本号、浏览器类型。为确保操作环境的安全或提供服务所需，我们会收集有关您使用的移动应用和其他软件的信息。</view>
			<view class="text title3">o3)IP地址</view>
			<view class="text title3">o4)服务日志信息</view>
			<view class="text">例如，您在使用我们服务时搜索、查看的信息、服务故障信息、引荐网址等信息。</view>
			<view class="text title3">o5)通讯日志信息</view>
			<view class="text">例如，您在使用我们服务时曾经通讯的账户、通讯时间和时长。</view>
			<view class="text title3">·位置信息</view>
			<view class="text">当您使用与位置有关的服务时，我们可能会记录您设备所在的位置信息，以便为您提供相关服务。</view>
			<view class="text">在您使用服务时，我们可能会通过IP地址 、GPS、WLAN（如 WiFi)或基站等途径获取您的地理位置信息；</view>
			<view class="text">您或其他用户在使用服务时提供的信息中可能包含您所在地理位置信息，例如您提供的帐号信息中可能包含的您所在地区信息，您或其他人共享的照片包含的地理标记信息。</view>
			<view class="text title3">·其他相关信息</view>
			<view class="text">为了帮助您更好地使用我们的产品或服务，我们会收集相关信息。</view>
			<view class="text">从第三方合作伙伴获取的信息</view>
			<view class="text">我们可能会获得您在使用第三方合作伙伴服务时所产生或分享的信息。例如，您使用微信登录、电子合同签约等第三方合作伙伴服务时，我们会获得您登录第三方合作伙伴服务的名称、登录时间，方便您进行授权管理。请您仔细阅读第三方合作伙伴服务的用户协议或隐私政策。</view>
			<view class="text">我们如何使用收集的信息</view>
			<view class="text">我们严格遵守法律法规的规定及与用户的约定，将收集的信息用于以下用途。若我们超出以下用途使用您的信息，我们将再次向您进行说明，并征得您的同意。</view>
			<view class="text title3">向您提供服务</view>
			<view class="text">满足您的个性化需求</view>
			<view class="text">例如，语言设定、位置设定、个性化的帮助服务。</view>
			<view class="text title3">产品开发和服务优化</view>
			<view class="text">例如，当我们的系统发生故障时，我们会记录和分析系统故障时产生的信息，优化我们的服务。</view>
			<view class="text title3">安全保障</view>
			<view class="text">例如，我们会将您的信息用于身份验证、安全防范、反诈骗监测、存档备份、客户的安全服务等用途。例如，您下载或安装的安全软件会对恶意程序或病毒进行检测，或为您识别诈骗信息。</view>
			<view class="text title3">管理软件</view>
			<view class="text">例如，进行软件认证、软件升级等。</view>
			<view class="text">邀请您参与有关我们服务的调查</view>
			<view class="text">为了让您有更好的体验、改善我们的服务或经您同意的其他用途，在符合相关法律法规的前提下，我们可能将通过某些服务所收集的信息用于我们的其他服务。例如，将您在使用我们某项服务时的信息，用于另一项服务中向您展示个性化的内容或广告、用于用户研究分析与统计等服务。</view>
			<view class="text">为了确保服务的安全，帮助我们更好地了解我们应用程序的运行情况，我们可能记录相关信息，例如，您使用应用程序的频率、故障信息、总体使用情况、性能数据以及应用程序的来源。我们不会将我们存储在分析软件中的信息与您在应用程序中提供的个人身份信息相结合。</view>
			<view class="text">我们如何使用cookie及相关技术</view>
			<view class="text">我们或我们的第三方合作伙伴，可能通过放置安全的Cookie及相关技术收集您的信息，目的是为您提供更个性化的用户体验和服务。我们会严格要求第三方合作伙伴遵守本政策的相关规定。</view>
			<view class="text">您也可以通过浏览器设置管理Cookie。但请注意，如果停用Cookie，您可能无法享受最佳的服务体验，某些服务也可能无法正常使用。</view>
			<view class="text">您如何管理自己的信息</view>
			<view class="text">您可以在使用我们服务的过程中，访问、修改和删除您提供的注册信息和其他个人信息，也可按照通知指引与我们联系。您访问、修改和删除个人信息的范围和方式将取决于您使用的具体服务。</view>
			<view class="text">例如，若您在使用地理位置相关服务时，希望停止分享您的地理位置信息，您可通过手机定位关闭功能、软硬件服务商及通讯服务提供商的关闭方式停止分享，建议您仔细阅读相关指引。</view>
			<view class="text">我们将按照本政策所述，仅为实现我们产品或服务的功能，收集、使用您的信息。</view>
			<view class="text">如您发现我们违反法律、行政法规的规定或者双方的约定收集、使用您的个人信息，您可以要求我们删除。</view>
			<view class="text">如您发现我们收集、存储的您的个人信息有错误的，您也可以要求我们更正。</view>
			<view class="text">请通过本政策列明的联系方式与我们联系。</view>
			<view class="text">在您访问、修改和删除相关信息时，我们可能会要求您进行身份验证，以保障帐号的安全。</view>
			<view class="text">请您理解，由于技术所限、法律或监管要求，我们可能无法满足您的所有要求，我们会在合理的期限内答复您的请求。</view>
			<view class="text">我们分享的信息</view>
			<view class="text">我们遵照法律法规的规定，对信息的分享进行严格的限制，例如：</view>
			<view class="text">经您事先同意，我们可能与第三方分享您的个人信息</view>
			<view class="text">仅为实现外部处理的目的，我们可能会与第三方合作伙伴（第三方服务供应商、承包商、代理、广告合作伙伴、应用开发者等，例如，代表我们发出电子邮件或推送通知的通讯服务提供商、为我们提供位置服务的地图服务供应商）（他们可能并非位于您所在的法域）分享您的个人信息，让他们按照我们的说明、隐私政策以及其他相关的保密和安全措施来为我们处理上述信息，并用于以下用途：</view>
			<view class="text">o· 向您提供我们的服务；</view>
			<view class="text">o· 实现“我们如何使用收集的信息”部分所述目的；</view>
			<view class="text">o· 履行我们在《网税宝服务协议》或本政策中的义务和行使我们的权利；</view>
			<view class="text">o· 理解、维护和改善我们的服务。</view>
			<view class="text">如我们与上述第三方分享您的信息，我们将会采用加密、匿名化处理等手段保障您的信息安全。</view>
			<view class="text">随着我们业务的持续发展，当发生合并、收购、资产转让等交易导致向第三方分享您的个人信息时，我们将通过推送通知、公告等形式告知您相关情形，按照法律法规及不低于本政策所要求的标准继续保护或要求新的管理者继续保护您的个人信息。</view>
			<view class="text">我们会将所收集到的信息用于大数据分析。</view>
			<view class="text">例如，我们将收集到的信息用于分析形成不包含任何个人信息的城市热力图或行业洞察报告。</view>
			<view class="text">我们可能对外公开并与我们的合作伙伴分享经统计加工后不含身份识别内容的信息，用于了解用户如何使用我们服务或让公众了解我们服务的总体使用趋势。</view>
			<view class="text">我们可能基于以下目的披露您的个人信息</view>
			<view class="text">o· 遵守适用的法律法规等有关规定；</view>
			<view class="text">o· 遵守法院判决、裁定或其他法律程序的规定；</view>
			<view class="text">o· 遵守相关政府机关或其他法定授权组织的要求；</view>
			<view class="text">o· 我们有理由确信需要遵守法律法规等有关规定；</view>
			<view class="text">o· 您自行向社会公众公开的信息</view>
			<view class="text">o· 从合法公开披露的信息中收集信息的，如合法的新闻报道、政府信息公开等渠道。</view>
			<view class="text">o· 为执行相关服务协议或本政策、维护社会公共利益，为保护我们的客户、我们或我们的关联公司、其他用户或雇员的人身财产安全或其他合法权益合理且必要的用途。</view>
			<view class="text">根据法律规定，共享、转让、披露经去标识化处理的个人信息，且确保数据接收方无法复原并重新识别信息主体的，不属于个人信息的对外共享、转让及公开披露行为，对此类数据的保存及处理将无需另行向您通知并征得您的同意。</view>
			<view class="text">我们可能向您发送的信息</view>
			<view class="text title3">信息推送</view>
			<view class="text">您在使用我们的服务时，我们可能向您发送电子邮件、短信、资讯或推送通知。</view>
			<view class="text">您可以按照我们的相关提示，在设备上选择取消订阅。</view>
			<view class="text">与服务有关的公告</view>
			<view class="text">我们可能在必要时（例如，因系统维护而暂停某一项服务时）向您发出与服务有关的公告。</view>
			<view class="text">您可能无法取消这些与服务有关、性质不属于广告的公告。</view>
			<view class="text">存储信息的地点和期限</view>
			<view class="text title3">存储信息的地点</view>
			<view class="text">我们遵守法律法规的规定，将境内收集的用户个人信息存储于境内。</view>
			<view class="text title3">存储信息的期限</view>
			<view class="text">一般而言，我们仅为实现目的所必需的最短时间保留您的个人信息。但在下列情况下，我们有可能因需符合法律要求，更改个人信息的存储时间：</view>
			<view class="text">· 为遵守适用的法律法规等有关规定；</view>
			<view class="text">· 为遵守法院判决、裁定或其他法律程序的规定；</view>
			<view class="text">· 为遵守相关政府机关或法定授权组织的要求；</view>
			<view class="text">· 我们有理由确信需要遵守法律法规等有关规定；</view>
			<view class="text">· 为执行相关服务协议或本政策、维护社会公共利益，为保护们的客户、我们或我们的关联公司、其他用户或雇员的人身财产安全或其他合法权益所合理必需的用途。</view>
			<view class="text">当我们的产品或服务发生停止运营的情形时，我们将采取例如，推送通知、公告等形式通知您，并在合理的期限内删除或匿名化处理您的个人信息。</view>
			<view class="text title3">信息安全</view>
			<view class="text">我们为您的信息提供相应的安全保障，以防止信息的丢失、不当使用、未经授权访问或披露。</view>
			<view class="text">我们严格遵守法律法规保护用户的通信秘密。</view>
			<view class="text">我们将在合理的安全水平内使用各种安全保护措施以保障信息的安全。</view>
			<view class="text">例如，我们使用加密技术（例如，TLS、SSL）、匿名化处理等手段来保护您的个人信息。</view>
			<view class="text">我们建立专门的管理制度、流程和组织确保信息安全。</view>
			<view class="text">例如，我们严格限制访问信息的人员范围，要求他们遵守保密义务，并进行审查。</view>
			<view class="text">若发生个人信息泄露等安全事件，我们会启动应急预案，阻止安全事件扩大，并以推送通知、公告等形式告知您。</view>
			<view class="text">请您务必妥善保管好您的账号、密码及其他身份要素。您在使用网税宝服务时，我们会通过您的账号、密码及及其他身份要素来识别您的身份。一旦您泄露了前述信息，您可能会蒙受损失，并可能对您产生不利。如您发现账号、密码及/或其他身份要素可能或已经泄露时，请您立即和我们取得联系，以便我们及时采取相应措施以避免或降低相关损失。</view>
			<view class="text title3">适用范围</view>
			<view class="text">我们的所有服务均适用本政策。</view>
			<view class="text">某些服务有其特定的隐私指引/声明，该特定隐私指引/声明更具体地说明我们在该服务中如何处理您的信息。</view>
			<view class="text">如本政策与特定服务的隐私指引/声明有不一致之处，请以该特定隐私指引声明为准。</view>
			<view class="text">请您注意，本政策不适用由其他公司或个人提供的服务。</view>
			<view class="text">例如，您通过使用微信帐号登录其他公司或个人提供的服务。</view>
			<view class="text">您使用该等第三方服务须受其隐私政策（而非本政策）约束，您需要仔细阅读其政策内容。</view>
			<view class="text title3">年龄限制</view>
			<view class="text">我们建议：任何未成年人参加网上活动应事先取得家长或其法定监护人（以下简称"监护人"）的同意。我们将根据国家相关法律法规的规定保护未成年人的相关信息。</view>
			<view class="text">我们鼓励父母或监护人指导未成年人使用网税宝服务。如果您是未成年人，请通知您的父母或监护人阅读本政策，并在您提交个人信息之前，寻求父母或监护人的同意和指导。</view>
			<view class="text title3">联系我们</view>
			<view class="text">如您对本政策或其他相关事宜有疑问，</view>
			<view class="text">请通过<a href="https://www.wangshuibao.com">https://www.wangshuibao.com</a>与我们联系。</view>
			<view class="text title3">变更</view>
			<view class="text">我们可能适时修订本政策内容，该等修改构成本政策的一部分。如该等更新造成您在本政策下权利的实质减少或重大变更，我们将在本政策生效前通过网站公告、页面显著位置提示、推送通知、弹窗提示、发送电子邮件或其他方式来通知您。在该种情况下，您如果不同意该等变更，可以选择停止使用网税宝服务；如您仍然继续使用网税宝服务的，即表示您已充分阅读、理解并同意受经修订的本政策的约束。</view>
		</view>
	</view>
</template>

